.device_laptop {
  display: inline-block;
  position: relative;
  z-index: 3;
  text-align: center;
  font-size: 0;
  perspective: 2400px;
  perspective-origin: 50% 100%;
  opacity: 0;
  transition: 500ms opacity;
}
.device_laptop img {
  display: block;
  max-width: 100%;
  backface-visibility: hidden;
}
.device_laptop.is-loaded {
  opacity: 1;
}
.device_laptop.is-opened .device_part .is-top {
  transform: translate3d(0, 0, -11px) rotateX(90deg) scale(1, 1);
}
.device_part {
  display: inline-block;
  position: relative;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  transform-origin: 50% 100%;
  transition: 900ms;
}
.device_part .is-top {
  transform-origin: 50% 0;
  transform: translate3d(0, 0, -11px) rotateX(90deg);
  transition: 900ms;
}
.device_part .is-bottom {
  transform-origin: 50% 0;
  transform: translate3d(0, 0, 0) rotateX(90deg);
}
.device_part .is-top,
.device_part .is-bottom {
  position: absolute;
  top: 0;
  left: 0;
}
.device_part.is-top {
  transform: translate3d(0, 0, 0) rotateX(-90deg);
}
.device_part.is-bottom {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate3d(0, 0, 0) rotateX(-90deg);
}
.device_cover {
  position: relative;
}
.device_screen {
  display: block;
  position: absolute;
  top: 6%;
  left: 1.5rem;
  width: 97%;
  border-radius: 6px;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 1px);
  max-height: 100%;
  overflow-y: scroll;
}
.device_screen img {
  width: 99%;
}

@media screen and (min-width: 1280px) {
  .device_screen img {
    width: 100%;
  }
}
@media screen and (max-width: 991px) and (min-width: 768px) {
  .device_screen {
    left: 1.25rem;
    width: 96%;
  }
  .device_screen img {
    width: 99%;
  }
}
@media screen and (max-width: 767px) {
  .device_screen {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    left: 0;
  }
  .device_screen img {
    width: 95%;
  }
}